<template>
  <div>
    <el-dialog :visible.sync="dialogVisible" width="800px" title="添加">
      <el-form label-width="130px">
        <el-form-item label="登陆账号">
          <el-input v-model="form.account" class="commen" />
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.pwd" class="commen" />
        </el-form-item>
        <el-form-item label="商户名称">
          <el-input v-model="form.busName" class="commen" />
        </el-form-item>
        <el-form-item label="logo">
          <el-upload ref="logo" action :http-request="uploadLogo" list-type="picture-card">
            <i slot="default" class="el-icon-plus" />
          </el-upload>
        </el-form-item>
        <el-form-item label="营业执照">
          <el-upload ref="license" action :http-request="uploadLicense" auto-upload list-type="picture-card">
            <i slot="default" class="el-icon-plus" />
          </el-upload>
        </el-form-item>
        <el-form-item label="社会信用统一代码">
          <el-input v-model="form.socialCredit" class="commen" />
        </el-form-item>
        <el-form-item label="法人">
          <el-input v-model="form.legal" class="commen" />
        </el-form-item>
        <el-form-item label="联系号码">
          <el-input v-model="form.phone" class="commen" />
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="form.address" class="commen" />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog :visible.sync="modifyStatus" width="800px" title="修改">
      <el-form label-width="130px">
        <el-form-item label="登陆账号">
          <el-input v-model="form.account" class="commen" :disabled="true" />
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.pwd" class="commen" />
        </el-form-item>
        <el-form-item label="商户名称">
          <el-input v-model="form.busName" class="commen" />
        </el-form-item>
        <el-form-item label="logo">
          <el-upload ref="logo" action :http-request="uploadLogo" list-type="picture-card">
            <i slot="default" class="el-icon-plus" />
          </el-upload>
        </el-form-item>
        <el-form-item label="营业执照">
          <el-upload ref="license" action :http-request="uploadLicense" auto-upload list-type="picture-card">
            <i slot="default" class="el-icon-plus" />
          </el-upload>
        </el-form-item>
        <el-form-item label="社会信用统一代码">
          <el-input v-model="form.socialCredit" class="commen" />
        </el-form-item>
        <el-form-item label="法人">
          <el-input v-model="form.legal" class="commen" />
        </el-form-item>
        <el-form-item label="联系号码">
          <el-input v-model="form.phone" class="commen" />
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="form.address" class="commen" />
        </el-form-item>
        <el-form-item label="备注">
          <el-input type="textarea" rows="7" v-model="form.remark" />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="modifyBus">确 定</el-button>
      </span>
    </el-dialog>
    <div class="sort-content">
      <header class="header">
        <div class="search-container">
          <div class="bus_name">
            <el-input placeholder="请输入商户名称" v-model="busName" />
          </div>
          <div class="search-btn" @click="serach">
            确定
          </div>
        </div>
        <div class="add" @click="dialogVisible= true">
          添加
        </div>
      </header>
      <el-table :data="busData.list" border style="width=100%" :header-cell-style="{ 'text-align': 'center', color: 'black' }" :cell-style="{ 'text-align': 'center' }">
        <el-table-column prop="id" label="序号" />
        <el-table-column prop="mch_name" label="商户名称" :show-overflow-tooltip="true" />
        <el-table-column label="法人" prop="legal" />
        <el-table-column prop="phone" label="联系号码" />
        <el-table-column prop="balance" label="余额" />
        <el-table-column label="状态" prop="desStatus" />
        <el-table-column prop="created_at" label="注册时间" :show-overflow-tooltip="true" />
        <el-table-column width="250px">
          <template slot="header" :slot-scope="scope">
            操作
          </template>
          <template slot-scope="scope">
            <el-button size="mini" @click="modify(scope.row.id)">
              修改
            </el-button>
            <el-switch
              style="margin-left: 30px"
              v-model="scope.row.status"
              active-text="启用"
              inactive-text="禁用"
              @change="onChange(scope.row.id)"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination layout="prev,pager,next" background :page-size="limit" :total="busData.total" @current-change="changePage" @prev-click="prev" @next-click="next" />
    </div>
  </div>
</template>

<script>
import { client, setImgName } from "@/config/oss";
export default {
    data() {
        return {
            dialogVisible:false,
            modifyStatus:false,
            // 商户Id
            modifyId:"",
            // 商户名称
            busName: "",
            form: {
                // 登陆账号
                account: "",
                pwd: "",
                // 商户名称
                busName: "",
                // logo
                avater: "",
                // 营业执照
                license: "",
                // 社会统一信用码
                socialCredit: "",
                // 法人
                legal: "",
                phone: "",
                address: "",
                // 备注
                remark:""
            },
            value: "",
            // 选中状态
            selectedStatus: "",
            // 开始时间
            startTime: "",
            // 结束时间
            endTime: "",
            scope: "scope",
            // 商户列表
            busData: [],
            limit: 10,
            pages: 1,
        };
    },
    created() {
        this.getBusList();
    },
    filters: {
        levelWord(value) {
            let userLevel = "";
            switch (value) {
            case 1:
                userLevel = "普通";
                break;
            case 2:
                userLevel = "消费者";
                break;
            case 3:
                userLevel = "代理";
                break;
            case 4:
                userLevel = "总代理";
                break;

            default:
                userLevel = "";
                break;
            }
            return userLevel;
        },
    },
    methods: {
    // 搜索
        serach() {
            if(!this.busName){
                return;

            }
            // 初始化
            this.pages = 1;
            this.getBusList();
        },
        async uploadLogo(file) {
            try {
                let url = await setImgName(client(), file);
                this.form.avater = url;
            } catch (error) {
                console.log(error);
            }
        },
        async uploadLicense(file) {
            try {
                let url = await setImgName(client(), file);
                this.form.license = url;
            } catch (error) {
                console.log(error);
            }
        },
        // 添加商户
        async confirm (){
            try {
                let res = await this.axios.post("/admin/addmch",{
                    "mobile":this.form.account,
                    "password":this.form.pwd,
                    "mch_name":this.form.busName,
                    "avater":this.form.avater,
                    "license":this.form.license,
                    "social_credit":this.form.socialCredit,
                    "legal":this.form.legal,
                    "phone":this.form.phone,
                    "address":this.form.address

                });
                this.close();
                this.pages = 1;
                this.getBusList();
                this.$message.success({
                    message:res.msg
                });
            } catch (error) {
                console.log(error);
            }

        },
        // 修改
        modify(id){
            this.modifyStatus = true;
            this.modifyId = id;
            
        },
        async onChange (id){
            await this.axios.post("/admin/updateMchState",{
                "user_id":id
            });
            this.getBusList();


        },
        // 修改商户
        async modifyBus (){
            try {
                let res = await this.axios.post("/admin/updatemch",{
                    "mch_id":this.modifyId,
                    "mobile":this.form.account,
                    "password":this.form.pwd,
                    "mch_name":this.form.busName,
                    "avater":this.form.avater,
                    "license":this.form.license,
                    "social_credit":this.form.socialCredit,
                    "legal":this.form.legal,
                    "phone":this.form.phone,
                    "address":this.form.address,
                    "remark":this.form.remark


                });
                this.close();
                this.$message.success({
                    message:res.msg
                });
                this.getBusList();
            } catch (error) {
                console.log(error);
            }

        },
        // 获取商户列表
        async getBusList() {
            try {
                let res = await this.axios.get("/admin/merchants", {
                    params: {
                        limit: this.limit,
                        pages: this.pages,
                        keyword: this.busName,
                    },
                });
                this.busData = Object.assign(res.data, {
                    list: res.data.list.map((item) => {
                        return Object.assign(item, {
                            status: item.status === 1 ? true : false,
                            desStatus: item.status === 1 ? "正常" : "禁用",
                        });
                    }),
                });
            } catch (error) {
                console.log(error);
            }
        },
        // 上一页
        prev() {
            this.pages--;
            this.getUserList();
        },
        // 翻页
        changePage(page) {
            this.pages = page;
            this.getUserList();
        },
        // 下一页
        next() {
            this.pages++;
            this.getUserList();
        },
        close (){
            this.dialogVisible = false;
            this.modifyStatus = false;
            this.form = {};
            this.$refs.logo.clearFiles();
            this.$refs.license.clearFiles();
            
        }
    },
    components: {},
};
</script>

<style lang="scss" scoped>
.commen {
  width: 400px;
}
.sort-content {
  height:800px;
  .header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    .search-container {
      display: flex;
      justify-content: space-between;
      width: 320px;
      .search-btn {
        $height: 40px;
        width: 80px;
        height: $height;
        line-height: $height;
        text-align: center;
        color: white;
        cursor: pointer;
        background: #f7961c;
        border-radius: 6px;
      }
    }
    .add {
      color: #f7961c;
      width: 95px;
      height: 30px;
      text-align: center;
      line-height: 30px;
      border: 1px solid #f7961c;
      border-radius: 5px;
      align-self: flex-end;
      cursor: pointer;
    }
    .through {
      $height: 35px;
      color: #f7961c;
      width: 100px;
      height: $height;
      text-align: center;
      line-height: $height;
      border: 1px solid #f7961c;
      border-radius: 6px;
      cursor: pointer;
    }
  }
  .pagination {
    margin-top: 15px;
  }
}
</style>
