import OSS from "ali-oss";


// 阿里云图片oss配置
export const client = () => new OSS(
    {
        region: "oss-cn-beijing",
        accessKeyId: "LTAI4G4GvvKZwgrgCVguwAHz",
        accessKeySecret: "mDZSsy2WAXNjZfCVJkAs2T0RtJOzNF",
        bucket: "suixinsuiyi"
    }
);

// 阿里云上传图片命名

export const setImgName = async (clientOss, file,folder) => {
    // 上传文件类型
    const suffixArr = file.file.name.split("."),
        suffix = suffixArr[suffixArr.length - 1],

        date = new Date(),
        // 文件夹
        day = String(date.getFullYear()) + (date.getMonth() + 1) + date.getDate(),
        // 文件名
        fileName = String(date.getFullYear()) + (date.getMonth() + 1) + date.getDate() + date.getMilliseconds();


    try {
        let res = await clientOss.put(
            `backstage/${day}/${folder}/${fileName}.${suffix}`,
            file.file,
            {
                headers: {
                    "Content-Disposition": "inline",
                    "Content-Type": suffix
                }
            }
        );
        console.log(res);
        return res.url;

    } catch (error) {
        console.log(error);
    }
};